import { createFeatureSelector, createReducer, createSelector, on } from '@ngrx/store';
import { IProduct } from '../product';
import * as AppState from '../../state/app.state'; 
import * as ProductAction from './product.action'
export interface State extends AppState.State {
    products: ProductState
}
export interface ProductState {
    showProductCode: boolean;
    currentProduct: IProduct;
    products: IProduct[];
}

const initialState : ProductState = {
    showProductCode: true,
    currentProduct: {
        productId: null,
        productName: '',
        productCode: '',
        releaseDate: '',
        description: '',
        price: null,
        starRating: null,
        imageUrl:''
    },
    products: []
}

const getFeatureSelector = createFeatureSelector<ProductState>('products');

export const getShowProductCode = createSelector(
    getFeatureSelector,
    state => state.showProductCode
)
export const getSelectedProduct = createSelector(
    getFeatureSelector,
    state => state.currentProduct
)
export const getupdatedProduct = createSelector(
    getFeatureSelector,
    state => state.currentProduct
)



export const productsReducer = createReducer<ProductState>(
    initialState,
    on(ProductAction.toggleProductCode, (state) :ProductState  => {
        return {
            ...state,
            showProductCode : !state.showProductCode
        }
    }),
    on(ProductAction.setCurrentProduct, (state, action) :ProductState  => {
        return {
            ...state,
            currentProduct : action.product
        }
    })
)