import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { IProduct } from './product'
import { ProductsService } from './products.service'
import { getSelectedProduct, getShowProductCode, getupdatedProduct, State } from './store/products.reducer';
import * as ProductAction from './store/product.action'
@Component({
    templateUrl: './products-list.component.html',
    styleUrls: ['./products-list.component.scss']
})

export class ProductsListComponent implements OnInit{
    pageTitle: string = 'Product List'
    imageWidth: number= 20;
    imageMargin: number= 2;
    showImage:boolean = false;
    displayCode: Boolean = false;
    _listFilter: string;
    errorMessage: string;
    filteredProducts: IProduct[];
    selectedProduct: IProduct;
    constructor(private store: Store<State>,private productsService: ProductsService) {
        this.store.select(getShowProductCode).subscribe(
          showProductCode =>  this.displayCode = showProductCode
        )
        this.store.select(getSelectedProduct).subscribe(
          currentProduct =>  this.selectedProduct = { ...currentProduct }
        )
        this.store.select(getupdatedProduct).subscribe(
          currentProduct =>  this.selectedProduct = { ...currentProduct },
          currentProduct => this.products = this.products.map( item => item.productId === currentProduct.productId ? currentProduct : item)
        )
    }
    get listFilter() : string {
        return this._listFilter
    }
    set listFilter(value: string) {
        this._listFilter = value
        this.filteredProducts = this.listFilter ? this.performFilter(value) : this.products
    }
    performFilter(filterBy: string) : IProduct[] {
        filterBy = filterBy.toLocaleLowerCase();
        return this.products.filter((item: IProduct) => item.productName.toLocaleLowerCase().indexOf(filterBy) !== -1)
    }
    products: IProduct[] = [];

    onNotify(value: number): void {
      alert(value)
    }
 
    getProduct(product : IProduct) : void {
      this.store.dispatch(ProductAction.setCurrentProduct({ product: product }))
    }

    changeCode() :void {
      this.store.dispatch(ProductAction.toggleProductCode())
    }

    ngOnInit(): void {
      this.productsService.getProducts().subscribe({
        next: products => {
          this.products = products
          this.filteredProducts = this.products
        },
        error: err => this.errorMessage = err
      })
      
      this.listFilter = ''
    }
}