import { createAction, props } from "@ngrx/store";
import { IProduct } from "../product";

export const toggleProductCode = createAction('[product] toggle the product code')
export const setCurrentProduct = createAction('[product] set current product',
props<{ product: IProduct} >() );
export const updateCurrentProduct = createAction('[product] update current product',
props<{ product: IProduct} >() );
export const clearCurrentProduct = createAction('[product] delete current product');
export const initializeCurrentProduct = createAction('[product] add new product');